.App {
  text-align: center;
}

body{
  background-color: #d4d4d4;
}

.displayInline{
  display: inline;
}

.titleStyle{
  width: 100%;
  border: none;
  padding: 0.3rem 0 0.2rem 0;
}

.taskInput{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.commitInputStyle{
  width: 65%;
  border: none;
  border-radius: 5px 0 0 5px;
  margin: 1rem 0 0 0;
  padding: 0.3rem;
}

.commitButtonStyle{
  margin: 1rem 0 0 0;
  background-color: #282c34;
  color: white;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 0.3rem 0.7rem 0.3rem 0.7rem;
}

.dateParagraph{
  color: #434d63;
  font-size:x-small;
  margin: 0 0 0.3rem 0;
}

.taskContainer{
  margin: 0 1rem 0 1rem;
}

.chekboxStyle{
  margin-right: 3px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
